import Vue from 'vue'
import VueRouter from 'vue-router'
import globalConfig from '@/utils/globalConfig'
import {Message} from 'element-ui'
import Login from '@/views/Login.vue'
// Vue.use(VueRouter)
// // 获取原型对象push函数
// const originalPush = VueRouter.prototype.push

// // 获取原型对象replace函数
// const originalReplace = VueRouter.prototype.replace

// // 修改原型对象中的push函数
// VueRouter.prototype.push = function push(location){
// return originalPush.call(this , location).catch(err=>err)
// }

// // 修改原型对象中的replace函数
// VueRouter.prototype.replace = function replace(location){
// return originalReplace.call(this , location).catch(err=>err)
// }

const routes = [
  {
    path:globalConfig.routerBase +'/login',
    name: 'login',
    component: () => import('@/views/Login.vue'),
  },
  {
    path:globalConfig.routerBase +'/layout',
    name: 'layout',
    component: () => import('@/views/Layout.vue'),
    children:[
      {
        path:globalConfig.routerBase +'/layout/superadmin',
        name:'superadmin',
        component:() => import('@/views/SuperAdmin.vue')
      },
      {
        path:globalConfig.routerBase +'/layout/planadmin',
        name:'planadmin',
        component:() => import('@/views/PlanAdmin.vue')
      },
      {
        path:globalConfig.routerBase +'/layout/processope',
        name:'processope',
        component:() => import('@/views/ProcessOpe.vue')
      },
      {
        path:globalConfig.routerBase +'/layout/examiner',
        name:'examiner',
        component:() => import('@/views/Examiner.vue')
      }
    ]
    
  },
  {
    path:'/',
    redirect:globalConfig.routerBase +'/login'
  },
]
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
 return originalPush.call(this, location).catch(err => err)
}
Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  // base: process.env.BASE_URL,
  // base:globalConfig.routerBase,
  base:'/',
  routes
})
// router.beforeEach((to,from,next) => {
//   let token = sessionStorage.getItem('token')
//   if(!token){
//     if(to.path !== globalConfig.routerBase +'/login'){
//       return next({path:globalConfig.routerBase +'/login'})
//     }else{
//       next()
//     }
//   }else{
//     if(to.path == globalConfig.routerBase +'/login'){
//       return next({path:globalConfig.routerBase +'/login'})
//     }
//     next()
//   }
// })
router.beforeEach((to,from,next) => {
  let token = JSON.parse(sessionStorage.getItem('token'))
  if(!token && to.path !== globalConfig.routerBase +'/login'){
    Message.error(`无访问权限，请先登录`)
    next({ path:globalConfig.routerBase +'/login'})
    // next()
  }else{
    next()
  }
})
export default router



