import axios from './index'
// import globalConfig from '@/utils/globalConfig'

// let url = globalConfig.devServer

function UserLogin(param){ //用户登录
    return axios.post(`/user/cuser_login`,param)
}
function ChangePW(param){ //用户修改密码
    return axios.post(`/user/user_change_pw`,param)
}
function ShowUserList(param){ //超级管理员页面用户列表
    return axios.post(`/user/show_users`,param)
}
function ChangeUser(param){ //超级管理员新增用户修改用户权限
    return axios.post(`/user/change_user`,param)
}
function DelUser(param){ //超级管理员新增用户首页获取绩效单列表
    return axios.post(`/user/remove_user`,param)
}
function AddNewUser(param){ //超级管理员新增用户
    return axios.post(`/user/new_user`,param)
}
function AllAuth(){ //展示所有权限
    return axios.post(`/user/show_auth`)
}
function SubTask(param){ //操作员移交任务
    return axios.post(`/operator/sub_task`,param)
}
function IsTaskPass(param){ //任务是否合格
    return axios.post(`/examiner/pass_status`,param)
}
function ProvideCheckSheet(param){ //检验员出具检验单
    return axios.post(`/examiner/check_card`,param)
}
function OperatorTaskList(param){ //操作员产品任务列表
    return axios.post(`/operator/show_all_task`,param)
}



function OperatorLeaderSubTask(param){ //操作员组长派发任务
    return axios.post(`/oleader/send_task`,param)
}



function OperatorMemberList(param){ //操作员组员姓名列表
    return axios.post(`/oleader/show_oprlists`,param)
}
function ExaminerSheetList(param){ //检验员任务检验单列表
    return axios.post(`/examiner/show_exa_task`,param)
}



function ExaminerStopTask(param){ //检验员终止任务
    return axios.post(`/examiner/stop_task`,param)
}


function PlannerProAndMisList(param){ //计划管理员产品和任务列表
    return axios.post(`/plan/prod/prod_task_lists`,param)
}
function DelProd(param){ // 计划管理员删除产品
    return axios.post(`/plan/prod/remove_prod`,param)
}
function SendMissions(param){ //计划管理员下发产品任务
    return axios.post(`/plan/prod/send_prod`,param)
}
function PlannerCheckTaskSheet(param){ //计划管理员查看任务检验单
    return axios.post(`/plan/stack/check_card`,param)
}
function UploadFile(param){ //上传文件给服务器
    return axios.post(`/plan/prod/new_prod`,param)
}
// function UploadFile(param){ //上传文件给服务器
//     return axios({url:`${url}/plan/prod/new_prod`, method: 'POST',
//     // responseType: 'blob', //不可省，非常重要
//     // 'Content-Type': 'multipart/form-data',
//     data:param})
// }
export {
    UserLogin,
    ChangePW,
    ShowUserList,
    ChangeUser,
    DelUser,
    AddNewUser,
    DelProd,
    OperatorLeaderSubTask,
    OperatorMemberList,
    SendMissions,
    AllAuth,
    SubTask,
    IsTaskPass,
    ProvideCheckSheet,
    PlannerCheckTaskSheet,
    OperatorTaskList,
    ExaminerSheetList,
    ExaminerStopTask,
    UploadFile,
    PlannerProAndMisList
}



