<template>
  <div class="login">
    <div class="loginBox">
      <h2>欢迎登录</h2>
      <div class="inputUsername">
        <el-input style="margin:.8rem 0;border:1px solid dodgerblue;" v-model="userN" placeholder="请输入账号"></el-input>
      </div>
      <div class="inputPassword">
        <el-input style="border:1px solid dodgerblue;" placeholder="请输入密码" v-model="passW" show-password></el-input>
      </div>
      <div class="changePw">
        <el-button @click="dialogVisible = true" type="text">修改密码</el-button>
        <el-dialog
          :visible.sync="dialogVisible"
          width="30%"
          :before-close="handleClose">
          <el-input placeholder="请输入用户名" v-model="userN" ></el-input>
          <el-input placeholder="请输入旧密码" v-model="oldPW" show-password></el-input>
          <el-input placeholder="请输入新密码" v-model="newPW" show-password></el-input>
          <el-button @click="saveNewPW" type="warning" align="center">保存</el-button>
        </el-dialog>
      </div>
      <div class="submit">
        <el-button type="primary" @click="loginBtn">登录</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import {Message} from 'element-ui'
import { UserLogin,ChangePW } from '@/api/request'
import globalConfig from '@/utils/globalConfig'
export default {
    data(){
        return {
            userN:'',
            passW:'',
            oldPW:'',
            newPW:'',
            dialogVisible: false,
        }
    },
    mounted(){
      window.addEventListener('keydown',this.keyDown)
    },
    methods:{
      loginBtn(){
        if (this.userN == "") {
          Message.error(`用户名不能为空`)
          return;
        }if (this.passW == "") {
          Message.error(`密码不能为空`)
          return;
        }else{
          let param = {
            'userName':this.userN,
            'passWord':this.passW
          }
          UserLogin(param).then((res) => {
            console.log('loginRes',res);
            if(res.data.result == 1){
              // 将登陆者token、id、账号名、权限都存到session
              sessionStorage.setItem('token',JSON.stringify(res.data.respData.token))
              sessionStorage.setItem('userID',JSON.stringify(res.data.respData.userID))
              sessionStorage.setItem('UN',JSON.stringify(res.data.respData.un))
              sessionStorage.setItem('auth',JSON.stringify(res.data.respData.auth))
              if(res.data.respData.auth == 1){
                this.$router.push({name:'superadmin'});
              }else if(res.data.respData.auth == 2){
                this.$router.push({name:'planadmin'});
              }else if(res.data.respData.auth == 3 || res.data.respData.auth == 5){
                this.$router.push({name:'processope'});
              }else if(res.data.respData.auth == 4){
                this.$router.push({name:'examiner'});
              }
            }else{
              Message.error(`${res.data.msg}`)
            }
          })
        }
      },
      saveNewPW(){
        let param = {
          'userName':this.userN,
          'oldPW':this.oldPW,
          'newPW':this.newPW
        }
        ChangePW(param).then((res) =>{
          console.log('修改密码res',res);
          if(res.data.result == 1){
            Message.success(res.data.msg)
            setTimeout(() => {
              this.dialogVisible = false
            }, 1500);
          }else{
            Message.error(res.data.msg)
          }
        })
      },
      handleClose(done){
        done()
      },
      keyDown(e){
        if(e.keyCode === 13){
          this.loginBtn()
        }
      }
      
  },
  destroyed(){
    window.removeEventListener('keydown',this.keyDown,false)
  }
}
</script>

<style lang="scss" scoped>
  .login{
    height: 100vh;
    background-image: url(../assets/login.jpg);
    background-size: cover;
    position: relative;
    .loginBox{
      position:absolute;
      width: 25%;
      border: 2px solid gray;
      border-radius: .7rem;
      right:8%;
      top:30%;
      padding: 1.5rem;
      ::v-deep .el-input__inner {
       border:1px solid dodgerblue;
       margin:.8rem 0;
     }
    }
    h2{
      color: dodgerblue;
    }
    .inputUsername,.inputPassword{
      width: 100%;
    }
    .el-button--text{
      color: red;
    }
   
  }
</style>


