import axios from 'axios'
import 'element-ui/lib/theme-chalk/index.css';
import globalConfig from '@/utils/globalConfig';
import domMessage from '@/utils/messageOnce'
const messageOnce = new domMessage()
const service = axios.create({
    timeout:1000*10,
})
// console.log('process.env.NODE_ENV',process.env.NODE_ENV);
console.log('是否是生产环境',globalConfig.isProdEnv);
// 请求拦截器，在请求头中加入token，所有请求发出前都会携带token
service.interceptors.request.use((config) => {

     if (config.url) {
        if (globalConfig.isProdEnv) {
         config.url = globalConfig.prodServer + config.url;
        } else {
         config.url = globalConfig.devServer + config.url;
        }
         }
    if(config.url){ 
        console.log('config.url',config.url);
        if(sessionStorage.getItem('token')){
            let token = JSON.parse(sessionStorage.getItem('token'))
            config.headers['Authorization'] = token
        }
    }
    return config;
},
)

// 响应拦截器
service.interceptors.response.use((res) => {
    const code = res.data.result
    if(code == -1004){
       Message.error(`登录过期，请重新登录`)
        window.location = '/'
    }else if( code == 1){
        return res
    }else{
        return Promise.resolve(res) //不加这条，如果result为0则打印res为undefined
    }
},(err) => {
    console.log('err',err);
    if(err.response){
        const status = err.response.status
        switch(status){
            case 401:
                messageOnce.error({
                    message:'登录过期，请重新登陆',
                    type:'warning'
                })
                window.location = '/'
                    break
                case 500:
                    messageOnce.error({
                        message:'服务器内部错误',
                        type:'error'
                    })
                    break
                    case 400:
                    messageOnce.error({
                        message:'请求参数错误',
                        type:'error'
                    })
                    break
                case 502:
                    messageOnce.error({
                        message:'服务器连接失败',
                        type:'error'
                    })
                    break
                case 504:
                    messageOnce.error({
                        message:'服务器连接超时',
                        type:'error'
                    })
                    break
                default:
                    messageOnce.error({
                        message:'其他错误',
                        type:'error'
                    })
                    break
        }
    }
    return Promise.reject(err)
})

export default service;



