// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App.vue'
import router from './router'
import 'element-ui/lib/theme-chalk/index.css';
import { Button,Input,Message,Table,TableColumn,Dialog,Select,Option,Container,MessageBox,Pagination,Dropdown,
  DropdownMenu,DropdownItem,Menu,Submenu,MenuItem,MenuItemGroup,Header,Aside,Main,Upload ,Form,FormItem} from 'element-ui'

Vue.config.productionTip = false
Vue.use(Button)
Vue.use(Input)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Dialog)
Vue.use(Select)
Vue.use(Option)
Vue.use(Container)
Vue.use(Pagination)
Vue.use(Dropdown)
Vue.use(DropdownMenu)
Vue.use(DropdownItem)
Vue.use(Menu)
Vue.use(Submenu)
Vue.use(MenuItem)
Vue.use(MenuItemGroup)
Vue.use(Header)
Vue.use(Aside)
Vue.use(Main)
Vue.use(Upload)
Vue.use(Form)
Vue.use(FormItem)
Vue.component(MessageBox.name,MessageBox)
Vue.prototype.$message = Message //解决message一进页面就弹空框
Vue.prototype.$confirm = MessageBox.confirm //vue原型对象上挂载confirm

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



