const serverUrl = '/managesys/server';
// const webUrl = '/api/managesys/web';
const webUrl = '/managesys/web';

export default{
     // 全局路由前缀地址
    routerBase:webUrl,
     // 生产环境服务器地址
    prodServer:serverUrl,
    // 开发环境服务器地址
    devServer:'/api'+serverUrl,
    // 是否是生产环境
    isProdEnv:process.env.NODE_ENV == 'production' && process.env.VUE_APP_PREVIEW !== 'true',
}


